.browseProducts .topLayer {
  padding: 0;
  padding: 1.5em 3em 0.5em 3em;
}

.browseProducts .wallHeader {
  font-size: 1.5rem;
  font-weight: 600;
}

.browseProducts .wallBreadCrumbs {
  font-weight: 600;
  padding: 0 0 1em 0;
}

.browseProducts .wallHeaderContent {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  margin-bottom: 2em;
}

.browseProducts .heading {
  padding: 0;
  margin: 0;
  vertical-align: top;
}

.browseProducts .toggleHideFilter {
  display: none;
  margin-left: auto;
}

.browseProducts .toggleHideFilter span {
  font-size: 1.1rem;
  font-weight: 500;
  vertical-align: bottom;
}

.browseProducts #filterIcon {
  vertical-align: top;
  margin-left: 1em;
  height: 20px;
  width: 20px;
}

.browseProducts .mainView {
  display: flex;
  flex-direction: row;
  column-gap: 0;
  padding: 0 3em 0 2.5em;
  justify-content: flex-start;
}

.browseProducts .mainViewMsg {
  font-size: 1.2rem;
  color: black;
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.browseProducts .products {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 0.5fr 0.5fr;
  row-gap: 3.5em;
  padding: 0;
  margin: 0;
  width: auto;
  flex: 1 1 0;
}

.browseProducts .filters {
  display: none;
  position: relative;
  margin: 0;
  padding: 0 3.6em 0 0.5em;
}

.browseProducts .toggleHideFilterBtn {
  background: none;
  border: none;
}

.browseProducts .toggleHideFilterBtn:hover {
  cursor: pointer;
}

@media (width > 960px) {
  .browseProducts .filters {
    display: block;
  }

  .browseProducts .products {
    width: 100%;
    grid-template-rows: auto auto auto;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .browseProducts .toggleHideFilter {
    display: block;
  }
}

