.productContainer,
.maxWidth {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-bottom: 4em;
  padding: 0 0.5em 0 0.5em;
  height: fit-content;
}

.productContainer .productImg,
.maxWidth .productImg {
  height: 100%;
  max-height: 100%;
}

.productContainer .productImgContainer,
.maxWidth .productImgContainer {
  background-color: rgb(246, 246, 246);
  aspect-ratio: 1;
  min-height: 100%;
}

.productContainer .productInfo,
.maxWidth .productInfo {
  margin: auto 0;
  justify-self: flex-end;
  margin-top: 0.5em;
  font-size: 1.4em;
}

.productContainer .productInfo p,
.maxWidth .productInfo p {
  margin-bottom: 0.3em;
}

.productContainer .productName,
.maxWidth .productName,
.productContainer .categoryName,
.maxWidth .categoryName {
  font-size: 1.1rem;
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.productContainer .categoryName,
.maxWidth .categoryName {
  color: grey;
  padding-bottom: 0.4em;
}

.productContainer .productPrice,
.maxWidth .productPrice {
  font-size: 1rem;
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.createAccount {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3em;
}

.createAccount .createAccount-form {
  width: 441px;
  text-align: center;
  padding-bottom: 3em;
}

.createAccount .item {
  flex: 1 1 0;
}

.createAccount h4 {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1.5em;
}

.createAccount label {
  display: block;
  text-align: start;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 1em;
}

.createAccount input,
.createAccount .inputError {
  outline: none;
  text-decoration: none;
  padding: 0.6em 0em;
  margin-bottom: 1em;
  border: 0.5px solid black;
  border-radius: 1px;
  font-size: 1.1rem;
  font-weight: 500;
  width: 100%;
}

.createAccount input:focus,
.createAccount .inputError:focus {
  border-bottom: 3px solid black;
}

.createAccount .inputError {
  border: 1px solid rgb(219, 0, 0);
}

.createAccount .inputError:focus {
  border-bottom: 3px solid rgb(219, 0, 0);
}

.createAccount .createAccountBtn {
  margin-top: 3em;
  background-color: black;
  color: white;
  padding: 2em 0em;
  width: 100%;
  font-weight: 700;
  border-style: none;
  justify-content: center;
  cursor: pointer;
}

.createAccount .createAccountBtn:hover {
  background-color: rgb(51, 51, 51);
}

.createAccount p {
  margin: 0;
  padding: 0%;
}

.createAccount .errorMsg {
  font-size: 0.9rem;
  font-weight: 400;
  padding: 0.5em 0;
  margin-top: -1em;
  text-align: start;
  color: rgb(219, 0, 0);
}

@media (max-width: 470px) {
  .createAccount .heading {
    text-align: center;
  }

  .createAccount .item {
    width: 90%;
  }
}

