footer {
  width: auto;
  background-color: black;
  overflow: hidden;
  padding: 0em 3.5em;
  margin: 3em 0 0 0;
}

footer img {
  width: 30px;
  height: 25px;
}

footer .footerBorder {
  padding: 0;
  margin: 0;
  border-color: gray;
  opacity: 0.1;
}

footer .footerContainer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 3.3em 0;
  margin-top: -0.5em;
  flex: 1 1 0;
  width: auto;
}

footer .footerContainer .brandName {
  color: white;
  font-size: 1.5em;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}

footer .footerContainer .footerLinks {
  width: 100%;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  text-align: left;
  justify-content: center;
  padding: 2em 0;
  column-gap: 5em;
}

footer .footerContainer .footerLinks ul,
footer .footerContainer .footerLinks .socialMedia li,
footer .footerContainer .socialMedia .footerLinks li {
  list-style: none;
}

footer .footerContainer .footerLinks li {
  margin-bottom: 1em;
  font-weight: 600;
}

footer .footerContainer .footerLinks h4 {
  color: white;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  margin-bottom: 2.5em;
}

footer .footerContainer .footerLinks a,
footer .footerContainer .footerLinks a:visited {
  font-size: 0.9rem;
  text-decoration: none;
  color: gray;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

footer .footerContainer .socialMedia {
  white-space: nowrap;
}

footer .footerContainer .socialMedia ul,
footer .footerContainer .socialMedia li {
  display: inline;
  list-style: none;
  margin: 0;
  padding: 0;
}

footer .footerContainer .socialMedia a {
  margin-right: 1em;
}

@media (max-width: 464px) {
  footer .footerContainer .brandName {
    margin: 0 auto;
  }

  footer .footerContainer .footerLinks {
    transform: scale(0.9);
  }

  footer .footerContainer .socialMedia {
    margin: 0 auto;
  }
}

