.homePage {
  overflow: hidden;
}

.homePage .heroImage {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  height: 525px;
  overflow: hidden;
  background: linear-gradient(rgba(255, 255, 255, 0.067), rgba(0, 0, 0, 0.6)),
    url("http://localhost:8080/housoku-images/hero/home/streetwear-dancing-filter.jpg") no-repeat;
  background-size: cover;
  background-position-y: 40%;
  background-position-x: center;
  /*    margin: 0 3.5em 2.75em 3.5em;
  transform: scale(1); */
  margin-bottom: 2.75em;
}

.homePage .heroContainer {
  border: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 2em;
  text-align: center;
  row-gap: 0.5em;
  margin: 0;
  padding: 0 0 3em 0;
}

.homePage .heroTitle {
  font-size: 1.95rem;
  font-weight: bold;
}

.homePage .heroSubTitle {
  font-size: 1.5rem;
  margin-bottom: 0.5em;
  color: white;
}

.homePage .heroBtn {
  display: block;
  background-color: rgb(255, 255, 255);
  color: black;
  padding: 0;
  border-radius: 15px;
  height: 60px;
  width: 130px;
  font-weight: 700;
  font-size: 1rem;
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
  border: 2px solid gray;
  text-decoration: none;
  cursor: pointer;
}

.homePage .heroBtn:hover {
  background-color: rgb(212, 212, 212);
}

