.roboto-thin {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-style: normal;
}

.roboto-light {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-style: normal;
}

.roboto-regular {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
}

.roboto-medium {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-style: normal;
}

.roboto-bold {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-style: normal;
}

.roboto-black {
    font-family: "Roboto", sans-serif;
    font-weight: 900;
    font-style: normal;
}


// 
@mixin ProductFont($fontWeight: 500) {
    font-family: "Outfit", sans-serif;
    font-optical-sizing: auto;
    font-weight: $fontWeight;
    font-style: normal;
}

@mixin Heading($fontWeight: 500) {
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    font-weight: $fontWeight;
    font-style: normal;
}

